import React, { Component, useState, useEffect, Fragment } from 'react';
import ReactDOM from 'react-dom';
import { component } from 'picoapp';
var Cookies = require('js-cookie');

function randomIntFromInterval(min, max) {
  // min and max included
  return Math.floor(Math.random() * (max - min + 1) + min);
}

const StockLeft = (props) => {
  const [stock, setStock] = useState(null);

  useEffect(() => {
    // console.log(props.parentNode.dataset);
    let previousStock = Cookies.get(`stockLeft-${props.parentNode.dataset.productId}`);
    if (previousStock) {
      setStock(previousStock);
    } else {
      let newStock = randomIntFromInterval(2, 15);
      setStock(newStock);
      Cookies.set(`stockLeft-${props.parentNode.dataset.productId}`, newStock);
    }
  }, []);

  return <div className="StockLeft">{stock && <p>Hurry, only {stock} left.</p>}</div>;
};

export default component((node, ctx) => {
  const element = <StockLeft ctx={ctx} parentNode={node} />;
  ReactDOM.render(element, node);

  // Unmount component on Pjax navigation
  // return function () {
  //   try {
  //     ReactDOM.unmountComponentAtNode(node);
  //   } catch (e) {
  //     console.error(e);
  //   }
  // };
});
